import React from 'react';
import Empty from '@common/empty';
import Pagination from '@common/pagination';
import Icon from '@common/icon';
// import Loading from '@common/loading';
import './index.css';

/**
* @description Tableloading/empty
* @props {columns} Array 
* @props {dataSource} Array , loadingdataSourceemptydataSource.length0
* @props {dataSourceKey} String key
* @props {pagination} ReactComponent , Pagination
* @props {emptyJsx} ReactComponent 
* @props {className} String 
* @return {Num} result 
*/
export default function Table(props) {
  const {
    columns = [],
    dataSource,
    className = '',
    emptyJsx = null,
    dataSourceKey = 'key',
    pagination
  } = props;

  const colgroup = columns.map(n => {
    return <col key={n.key}
      width={n.width} />;
  });
  const header = columns.map(n => {
    return (
      <th key={n.key}>{n.title}</th>
    );
  });
  if (!Array.isArray(dataSource)) {
    return <div className="Table_loading">
      <Icon type="loading" />
    </div>;
  }
  if (dataSource.length === 0) {
    return emptyJsx || <Empty style={{ borderTop: 'none' }} />;
  }
  const tbody = dataSource.map(n => {
    return (
      <tr key={n[dataSourceKey]}>
        {columns.map(k => {
          return <td key={k.key}>
            {k.render ? k.render(n) : n[k.key]}
          </td>;
        })}
      </tr>
    );
  });
  return (
    <div>
      <table className={`table ${className}`}>
        <colgroup>{colgroup}</colgroup>
        <thead>
          <tr className="table-header">{header}</tr>
        </thead>
        <tbody className="table-tbody">{tbody}</tbody>
      </table>
      {pagination && <Pagination {...pagination} />}
    </div>
  );
};