import React, { useReducer, useEffect, useState } from 'react';
import Chapter from './chapter';
import ChapterHeader from './chapter-header';
import Catalog from './catalog';
import { catalogContext, catalogReducer, initialState } from '@context/catalog-context';
import { parseUrlQuery } from '@util/util';
import './index.css';

export default function ChapterWrapper({
  spaceInfo = {},
  userInfo = {}
}) {
  const [state, dispatch] = useReducer(catalogReducer, initialState);
  const [catalog, setCatalog] = useState([]);
  const { type = '' } = parseUrlQuery();

  const updateCatalog = (payload) => {
    dispatch({
      type: 'UPDATE_CATALOG',
      payload
    });
  };

  useEffect(() => {
    try {
      const d = JSON.parse(spaceInfo.space.catalog);
      updateCatalog({ catalog: d, docs: spaceInfo.docs });
      setCatalog(d);
    } catch (error) {
    }
  }, [spaceInfo.space]);
  // spacejsonspace
  // 1.json
  // 2. json
  // 3. level
  // 4. 2level(1)childs

  // const catalog = [{
  //   display_level: 0, // 
  //   max_level: 3, // 
  //   type: 'META' // meta
  // }, {
  //   docId: '11', // id
  //   level: 0, // level=0level=1offset=40
  //   status: '0', // 0 1
  //   type: 'DOC' // 
  // }];
  return (
    <catalogContext.Provider value={{
      info: state,
      updateCatalog
    }}>
      <div className="Chapter">
        <ChapterHeader
          space={spaceInfo.space}
          userInfo={userInfo} />
        {
          type.toLocaleLowerCase() === 'toc'
            ? <Chapter />
            : <Catalog
              catalog={catalog}
              docs={spaceInfo.docs} />
        }
      </div>
    </catalogContext.Provider>
  );
};