import React from 'react';
import ReactDOM from 'react-dom';
import Icon from '@common/icon';
import Button from '@common/button';
import confirm from './confirm';
import './index.css';

const fn = () => { };
export default function Modal({
  children = '',
  wrapClassName = '', // 
  visible = false, // 
  width = 520, // 
  onCancel = fn, // 
  onConfirm = fn, // 
  title = '', // 
  subTitle = '', // 
  mask = true, //  
  footer = null, //  footer={'none'}
  closeIcon = null, // 
  closable = true, // 
  cancelText = '', // 
  confirmText = '' // 
}) {
  const w = isNaN(width) ? width : `${width}px`;

  const defaultFooter = (
    <div className="Modal_Footer flex">
      <Button onClick={onCancel}>{cancelText}</Button>
      <Button type="primary"
        onClick={onConfirm}>{confirmText}</Button>
    </div>
  );
  const footerJsx = footer === 'none' ? null : (footer || defaultFooter);
  const closeJsx = closable && (closeIcon || (
    <Icon type="close"
      onClick={onCancel}
      className="Modal_Close" />
  ));
  let classes = 'Modal_Mask animated ';
  classes += `${mask ? 'Modal_Mask_Bg' : ''} `;
  classes += `${visible ? 'Modal_Show' : ''} `;
  return ReactDOM.createPortal(
    (<div className={classes}>
      <div className={`Modal ${wrapClassName}`}
        style={{
          width: w
        }}>
        <div className="Modal_Header">
          <span>{title}</span>
          <p>{subTitle}</p>
        </div>
        {closeJsx}
        <div className="Modal_Body">
          {children}
        </div>
        {footerJsx}
      </div>
    </div>)
    , document.body);
};

Modal.confirm = confirm;