import React, { useEffect, useRef } from 'react';
import Icon from '@common/icon';
import './index.css';

/**
* @description 
* @props {placeholder} String input placeholder
* @props {className} String 
* @props {onChange} Function input onChange(e)
* @props {onEnter} Function inputenter  onEnter(input.current.value);
*/
export default function Search({
  className = '',
  placeholder = '',
  onChange = () => { },
  onEnter = () => { }
}) {
  const input = useRef(null);
  useEffect(() => {
    input.current.addEventListener('keyup', (e) => {
      if (+e.keyCode === 13) {
        onEnter(input.current.value);
      }
    }, false);
  }, []);
  return (
    <div className={`Search ${className}`}>
      <Icon type="search" />
      <input type="text"
        ref={input}
        placeholder={placeholder}
        autoComplete="off"
        spellCheck="true"
        onChange={onChange}
        className="Search_input" />
    </div>
  );
}